import React from 'react';
import PageWrapper from '../components/PageWrapper';
import { useAuthEffects } from '../components/Auth';
import { Container, Divider, Grid2, ImageListItem, ImageList, useMediaQuery, ImageListItemBar, useTheme, IconButton } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import { useNavigate } from 'react-router-dom';
import itemDataNew from '../demoContent';

const DiscoverView = () => {
	useAuthEffects();

	const theme = useTheme();
	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));
	const navigate = useNavigate();

	return <PageWrapper>
		<Container>
			<Grid2 container spacing={8}>
				<Grid2 size={12}>
					<Divider sx={{mt: 1}}/>
					<ImageList cols={isSmUp ? 3 : 1} gap={8} rowHeight={400}>
						{itemDataNew.map((item) => {
							const hoursInfo = <span>&middot; {Math.round(Math.random() * 10)} hours ago</span>;
							return <ImageListItem key={item.img} onClick={() => navigate(`/plans/${item.id}`)} sx={{cursor: 'pointer'}}>
								<img
									srcSet={`${item.img}?w=248&fit=crop&auto=format&dpr=2 2x`}
									src={`${item.img}?w=248&fit=crop&auto=format`}
									alt={item.title}
									loading="lazy"
									style={{overflow:'hidden'}}
								/>
								<ImageListItemBar
									title={item.title}
									subtitle={<><span>by: {item.author}</span>{hoursInfo}</>}
									actionIcon={
										<IconButton
											sx={{ color: 'rgba(255, 255, 255, 0.54)' }}
											aria-label={`info about ${item.title}`}
										>
											<AddIcon />
										</IconButton>
									}
									position='below'
								/>
							</ImageListItem>;
						})}
					</ImageList>
				</Grid2>
			</Grid2>
		</Container>
	</PageWrapper>;
};

export default DiscoverView;
