function srcset(image: string, size: number, rows = 1, cols = 1) {
	return {
		src: `${image}?w=${size * cols}&h=${size * rows}&fit=crop&auto=format`,
		srcSet: `${image}?w=${size * cols}&h=${
			size * rows
		}&fit=crop&auto=format&dpr=2 2x`,
	};
}

  interface TabPanelProps {
	children?: React.ReactNode;
	index: number;
	value: number;
  }
  
function CustomTabPanel(props: TabPanelProps) {
	const { children, value, index, ...other } = props;
  
	return (
		<div
			role="tabpanel"
			hidden={value !== index}
			id={`simple-tabpanel-${index}`}
			aria-labelledby={`simple-tab-${index}`}
			{...other}
		>
			{value === index && <Box sx={{ mt: 3 }}>{children}</Box>}
		</div>
	);
}

function a11yProps(index: number) {
	return {
		id: `simple-tab-${index}`,
		'aria-controls': `simple-tabpanel-${index}`,
	};
}

const PlanView = () => {
	useAuthEffects();

	const theme = useTheme();
	const isSmUp = useMediaQuery(theme.breakpoints.up('md'));
	const item = demoData[0];
	const [value, setValue] = React.useState(0);
	const materialsListRef = useRef<any>(null);

	const handleChange = (event: React.SyntheticEvent, newValue: number) => {
		setValue(newValue);
	};

	const [selectedMaterials, setSelectedMaterials] = useState(new Set());
	const selectedItemsCount = useMemo(() => selectedMaterials.size, [selectedMaterials]);

	const [selectedSupplierId, setSelectedSupplierId] = useState<string | null>(null);
	const selectedSupplier = useMemo(() => suppliersData.find((item) => item.id === selectedSupplierId), [selectedSupplierId]);

	useEffect(() => {
		if (materialsData && selectedSupplierId) {
			setSelectedMaterials(materialsData.reduce((acc, material) => acc.add(material.id), new Set()));
		}
	}, [materialsData, selectedSupplierId]);

	const handleProductCheckboxClick = (materialId: string) => {
		if (selectedMaterials.has(materialId)) {
			selectedMaterials.delete(materialId);
		} else {
			selectedMaterials.add(materialId);
		}
		setSelectedMaterials(new Set(selectedMaterials));
	};

	const handleSupplierClick = (supplierId: string) => {
		if (supplierId !== selectedSupplierId) {
			setSelectedSupplierId(supplierId);
		}
		if (materialsListRef.current) {
			materialsListRef.current.scrollIntoView({ behavior: 'smooth', block: 'start' });
		}
	};

	const DrawerHeightEl = () => <Box sx={{height: isSmUp ? 145 : 196}}></Box>;

	return <PageWrapper>
		<Container>
			<Box display={isSmUp ? 'flex' : undefined}>
				<Box flex={isSmUp ? 1 : undefined}>
					<Typography variant='h2' sx={{flex:1}}>{item.title}</Typography>
					<Typography variant='caption'>by {item.author}</Typography>
				</Box>
				{isSmUp && <Box>
					<IconButton size='large'>
						<AddIcon />
					</IconButton>
				</Box>}
			</Box>
			{/* <Box sx={{ width: '100%' }}> */}
			<Box sx={{ borderBottom: 1, borderColor: 'divider' }} ref={materialsListRef}>
				<Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
					<Tab label="Plan" {...a11yProps(0)} />
					{/* <Tab label="Instructions" {...a11yProps(1)} /> */}
					<Tab label="Supplies" {...a11yProps(1)} />
				</Tabs>
			</Box>
			<CustomTabPanel value={value} index={0}>
				<Grid2 container direction='row' spacing={2}>
					<Grid2 size={isSmUp ? 6 : 12}>
						<Typography variant='body1'>{item.description}</Typography>
						<List>
							<ListItem disablePadding>
								<ListItemIcon sx={{mr:2, minWidth: 0}}>
									<MoneyIcon />
								</ListItemIcon>
								<ListItemText primary={item.materialsCost} />
							</ListItem>
							<ListItem disablePadding>
								<ListItemIcon sx={{mr:2, minWidth: 0}}>
									<AccessTimeIcon />
								</ListItemIcon>
								<ListItemText primary={item.timeCost} />
							</ListItem>
							<ListItem disablePadding>
								<ListItemIcon sx={{mr:2, minWidth: 0}}>
									<SentimentVerySatisfiedIcon />
								</ListItemIcon>
								<ListItemText primary={item.ratingsSummary} />
							</ListItem>
						</List>
					</Grid2>
					<Grid2 size={isSmUp ? 6 : 12}>
						<ImageList
							variant="quilted"
							cols={4}
							rowHeight={100}
							sx={{m:0}}
						>
							{productImages.map((productImage) => (
								<ImageListItem key={productImage.img} cols={productImage.cols || 1} rows={productImage.rows || 1} sx={{borderRadius: 2, overflow: 'hidden', boxShadow: 3}}>
									<img
										{...srcset(productImage.img, 250, productImage.rows, productImage.cols)}
										alt={item.title}
										loading="lazy"
										style={{overflow:'hidden'}}
									/>
								</ImageListItem>
							))}
						</ImageList>
					</Grid2>
					<Grid2 size={12}>
						<Typography>How-to</Typography>
					</Grid2>
					{instructionsVideos.map((video) => (<Grid2 size={isSmUp ? 6 : 12} key={video.youtubeId}>
						<YouTubeEmbed videoId={video.youtubeId} title="Sample Video" />
					</Grid2>))}
					<Grid2 size={12}>
						<Box display='flex' flexDirection='column' alignItems='center' sx={{mt: 4}}>
							<Avatar sx={{width:125, height: 125}}></Avatar>
							<Typography variant='h5' sx={{mt:2}}>{item.author}</Typography>
							<Typography variant='body1' maxWidth={560} sx={{mt:2}}>
								Cinema Rigs, Camera reviews, Accessories, tutorials, tips, tricks and more!
								Videographer and photographer from Ireland based in the US. 
							</Typography>
						</Box>
					</Grid2>
				</Grid2>
			</CustomTabPanel>
			<CustomTabPanel value={value} index={1}>
				{/* Materials */}
				<Typography variant='body2' sx={{mt:2}}>Materials</Typography>
				<List>
					{materialsData.map((material,i) => (
						<>
							<ListItem key={i} sx={{display:'flex', mt: i !== 0 ? 2 : 0}}>
								<ListItemAvatar>
									<Avatar src={material.img} variant='square' sx={{ width: 96, height: 96, mr: 2, borderRadius: 2 }}>
										<FolderIcon />
									</Avatar>
								</ListItemAvatar>
								<ListItemText
									primary={material.name}
									secondary={material.description}
								/>
								<Box sx={{p:2, flex: 0}}>
									{material.quantity}{material.uom}
								</Box>
								{/* <Box>
									{selectedSupplierId ? <ExpandLess /> : <ExpandMore />}
								</Box> */}
							</ListItem>
							<Collapse in={Boolean(selectedSupplierId)} timeout="auto" unmountOnExit>
								<List component="div" disablePadding>
									<ListItemButton onClick={() => handleProductCheckboxClick(material.id)}>
										<Checkbox
											disabled={!selectedSupplierId}
											// checkedIcon={<ShoppingCartIcon />}
											checked={selectedMaterials.has(material.id)}
										/>
										<ListItemText primary='Ask offer'/>
									</ListItemButton>
								</List>
								<Divider />
							</Collapse>
						</>
					))}
				</List>
				{/* Tools */}
				<Typography variant='body2' sx={{mt:2}}>Tools</Typography>
				<List>
					{toolsData.map((tool,i) => (
						<>
							<ListItem key={i} sx={{display:'flex', mt: 2}}>
								<ListItemAvatar>
									<Avatar src={tool.img} variant='square' sx={{ width: 96, height: 96, mr: 2, borderRadius: 2 }}>
										<FolderIcon />
									</Avatar>
								</ListItemAvatar>
								<ListItemText
									primary={tool.name}
									secondary='huh'
								/>
								{/* <Box>
									{selectedSupplierId ? <ExpandLess /> : <ExpandMore />}
								</Box> */}
							</ListItem>
							<Collapse in={Boolean(selectedSupplierId)} timeout="auto" unmountOnExit>
								<List component="div" disablePadding>
									<ListItemButton sx={{ display:'flex' }} onClick={() => handleProductCheckboxClick(tool.id)}>
										<Checkbox
											disabled={!selectedSupplierId}
											// checkedIcon={<ShoppingCartIcon />}
											checked={selectedMaterials.has(tool.id)}
										/>
										<ListItemText primary='Ask offer'/>
									</ListItemButton>
								</List>
								{i < toolsData.length - 1 && <Divider />}
							</Collapse>
						</>
					))}
				</List>
				{/* Suppliers */}
				<Typography variant='body2' sx={{mt:3}}>
					Available suppliers
				</Typography>
				<Grid2 container spacing={2} sx={{mt:2}} justifyContent='center'>
					{suppliersData.map((supplier) => (
						<Grid2 key={supplier.id} size={isSmUp ? 4 : 10} justifyItems='center'>
							<Card 
								sx={{ maxWidth: 375, opacity: selectedSupplierId && selectedSupplierId !== supplier.id  ? 0.3 : 1, cursor: 'pointer', userSelect: 'none' }}
								onClick={() => handleSupplierClick(supplier.id)}
							>
								<CardMedia
									component="img"
									alt="green iguana"
									height="140"
									image={supplier.img}
								/>
								<CardContent>
									<Typography gutterBottom variant="h5" component="div">
										{supplier.name}
									</Typography>
									<Typography variant="body2" sx={{ color: 'text.secondary' }}>
										{supplier.description}
									</Typography>
									<List>
										<ListItem disablePadding>
											<ListItemIcon sx={{minWidth: 0, mr: 2}}>
												<SentimentVerySatisfiedIcon />
											</ListItemIcon>
											<ListItemText primary={supplier.ratingsSummary} />
										</ListItem>
									</List>
								</CardContent>
								<CardActions>
									{/* {supplier.directOrder ? <Button size="small" endIcon={<BoltIcon />}>View offer</Button> : null } */}
									<Button 
										startIcon={supplier.id !== selectedSupplierId ? null : <DoneIcon />} 
										size="small" 
										onClick={supplier.id === selectedSupplierId ? () => setSelectedSupplierId(null) : undefined}>
										{supplier.id === selectedSupplierId ? 'Selected' : 'Tap to select'}
									</Button>
								</CardActions>
							</Card>
						</Grid2>
					))}
				</Grid2>
				{Boolean(selectedSupplierId) && <DrawerHeightEl />}
				<Drawer
					variant='persistent'
					anchor='bottom'
					open={Boolean(selectedSupplierId)}
					PaperProps={{
						elevation: 1
					}}
				>
					<Grid2 container spacing={2} padding={2} pb={3}>
						<Grid2 size={isSmUp ? 'grow' : 12}>
							<Box display='flex' alignItems='center'>
								<IconButton onClick={() => setSelectedSupplierId(null)}><CloseIcon /></IconButton>
								<Box display='inline'>
									Requesting <Box display='inline' color={theme.palette.primary.main} fontWeight='bold'>{selectedItemsCount}</Box> items from <Box display='inline' color={theme.palette.primary.main} fontWeight='bold'>{selectedSupplier?.name}</Box>
								</Box>
							</Box>
							<TextField label='Additional notes to supplier' placeholder='Hi there 👋, ' multiline fullWidth sx={{mt:1, maxWidth: 520}} />
						</Grid2>
						<Grid2 size={isSmUp ? 'auto' : 12} textAlign='right'>
							<Button startIcon={<ShoppingCartCheckoutIcon />} disabled={!selectedItemsCount}>Proceed to order</Button>
						</Grid2>
					</Grid2>
				</Drawer>
			</CustomTabPanel>
		</Container>
	</PageWrapper>;
};

